.menu {
  position: fixed;
  color: $main-text-color;
  background-color: #c2c2c2;
  box-sizing: border-box;
  height: 100vh;
  font-weight: 600;
  text-transform: uppercase;
  z-index: 1000;
  padding-left: 72px;
  width: 100%;

  &__nav {
    margin-bottom: 40px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  &__phone-number {
    font-size: 16px;
    margin-bottom: 16px;
    line-height: 27px;
    letter-spacing: 0;
    display: block;
    color: inherit;
    text-decoration: none;
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $main-text-color;

    color: inherit;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
  }
}
