.comparebikes {
  &__products {
    display: grid;
    row-gap: 40px;

    @include on-tablet {
      justify-items: center;
     
    }

    @include on-desktop {
      grid-template-columns: repeat(3, 1fr);
      column-gap: 24px;
      justify-items: stretch;
      flex-direction: row;
    }
  }
}
